import "./Nav.css";
import image from "../../assest/rostro-rectangular.jpg";

const Nav = () => {
  return (
    <div className="containernav">
      <div className="containerizqnav">
        <input type="text" placeholder="Search" className="navinput" />
        <hr />
        <ion-icon size="large" name="search-outline"></ion-icon>
      </div>
      <div className="containerdernav">
        <div className="avatar">
          <img src={image} alt="" />
        </div>
        <div className="logout">
          <button type="button" className="navLogout">
            Logout
          </button>
        </div>
      </div>
      <div className="headerNav">
        <img
          src="https://prod.assets.earlygamecdn.com/images/resident-evil-4-remake-keyart.jpeg?transform=article3x_webp"
          alt=""
        />
        
      </div>
    </div>
  );
};

export default Nav;
